import React, { Component } from 'react';
import Card from'./CardUI';
import img1 from '../assets/istockphoto-1193323373-612x612.jpg'
import img2 from '../assets/download (1).jfif';
import img3 from '../assets/download (2).jfif';
import img4 from '../assets/download (3).jfif';

class Cards extends Component{
    render(){
        return(
            <div className="container-fluid d-flex justify-content-center">
                <div className="row">
                    <div className="col-md-3">
                        <Card imgsrc={img1} title="1" website=" "/>
                    </div>
                    <div className="col-md-3">
                        <Card imgsrc={img3} title="2" website=" "/>
                    </div>
                    <div className="col-md-3">
                        <Card imgsrc={img2} title="3" website=" "/>
                    </div>
                    <div className="col-md-3">
                        <Card imgsrc={img1} title="4" website=" "/>
                    </div>
                </div>
                
            </div>
        );
    }
}

export default Cards;