const ReadBook =()=> {
    console.log(5);
}
const Card = props =>{
    return(
        <div className="card text-center">
            <div className="overflow">
                <img src={props.imgsrc} alt="image1" className='card-img-top' />
            </div>
            <div className="card-body text-dark">
                <h4 className="card-title">{props.title}</h4>
                <p className="card-text text-secondary">
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Quaerat nisi
                    distinctio iste, qui illo suscipit placeat nam inventore vitae hic fugiat
                    
                </p>
                {/* <button   className='btn btn-outline-success' >Read Book</button> */}
                {/* <a href={props.website}>
                    <button className='btn btn-outline-success'>Click Here </button>
                </a> */}
                <button onClick={ReadBook} className='btn btn-outline-success'>
                    Read</button>
            </div>
        </div>
    );
}

export default Card;