import React, { useState } from 'react';
import { Viewer } from '@react-pdf-viewer/core';
import { defaultLayoutPlugin } from '@react-pdf-viewer/default-layout';
import '@react-pdf-viewer/core/lib/styles/index.css';
import '@react-pdf-viewer/default-layout/lib/styles/index.css';
import { Worker } from '@react-pdf-viewer/core';

import pd1 from '../assets/pdf1001.pdf';
import pd2 from '../assets/pdf1002.pdf';
import pd3 from '../assets/pdf1003.pdf';
import pd4 from '../assets/pdf1004.pdf';


export const Pdf2 = () => {
    const [defaultpdfFile]= useState(pd2);
    const defaultLayoutPluginInstance = defaultLayoutPlugin();
    return(
        <div className='pdf-container'>
            {defaultpdfFile&&<><Worker workerUrl='https:/unpkg.com/pdfjs-dist/build/pdf.worker.min.js'>
            <Viewer fileUrl={defaultpdfFile}
                plugins={[defaultLayoutPluginInstance]} ></Viewer>
            </Worker></>}
        </div>
    )
}

export default Pdf2;